import _ from 'lodash';
import { utils, BigNumber } from 'ethers';
import {
  PANCAKE_FACTORY,
  PANCAKE_HASH_CODE,
  APE_FACTORY,
  APE_HASH_CODE,
} from '../config/web3Config';
import { DApp } from '../model/tokenModel';
import { IRouteSwap, IDAppConfig } from '../model/swapModel';

function getClassWarningPriceImpact(value: number): string {
  if (value >= 0 && value < 3) {
    return '';
  } else if (value >= 3 && value < 10) {
    return 'fk-swap-text-warning';
  } else {
    return 'fk-swap-text-error';
  }
}

function getClassWarningFee(value?: number): string {
  if (!value) {
    return 'fk-swap-text-info';
  } else if (value === -1 || value <= 5) {
    return 'fk-swap-text-warning';
  } else {
    return 'fk-swap-text-error';
  }
}

function validRouteSwaps(routeSwaps: IRouteSwap[]): boolean {
  return !routeSwaps.some((routeSwap: IRouteSwap): boolean => {
    return !routeSwap.address;
  });
}

function getDappConfig(dApp: DApp): IDAppConfig {
  let config: IDAppConfig;

  switch (dApp) {
    case DApp.PANCAKE:
      config = {
        factory: PANCAKE_FACTORY,
        hashCode: PANCAKE_HASH_CODE,
      };
      break;

    case DApp.APE:
      config = {
        factory: APE_FACTORY,
        hashCode: APE_HASH_CODE,
      };
      break;
  }

  return config;
}

function parseUnitWithDecimals(
  value: string,
  decimals: number,
  loss: number = 0
): BigNumber {
  let valueFormat: string = value;
  let valueArr: string[] = _.split(value, '.');

  if (_.size(valueArr[1]) > decimals) {
    valueArr[1] = valueArr[1].slice(0, decimals);
    valueFormat = _.join([valueArr[0], valueArr[1]], '.');
  }

  let valueWithLossBN: BigNumber = utils.parseUnits(valueFormat, decimals);

  if (!!loss) {
    valueWithLossBN = valueWithLossBN.mul(100 - loss).div(100);
  }

  return valueWithLossBN;
}

const helper = {
  getClassWarningPriceImpact,
  getClassWarningFee,
  validRouteSwaps,
  getDappConfig,
  parseUnitWithDecimals,
};

export default helper;
